
import VHeader from '@/components/v-header/v-header'
import imageTop from '@/assets/img/default_avatar.png'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Sticky from 'vux/src/components/sticky/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

import Scroll from '@/components/scroll/scroll'
import axios from '@/api/axiosApi'
import { localUser } from '@/assets/js/local'
import { mapGetters, mapMutations } from 'vuex'
import moment from 'moment'

export default {
data () {
return {
imageTop: imageTop,
listOrder: ['退款中', '已退款', '退款失败'],
listOrders: [],
index: 0,
scrollHeight: ''
}
},
mounted () {

this.scrollHeight = window.innerHeight - 64 - 44
},
created () {
this.pageNum = 1
this.pageSize = 10
this.getWaitPayList(true)
},
computed: {
...mapGetters({
selectTab: 'orderQuitTab'
})
},
watch: {
'$route' (to, from) {
if (to.name === 'OrderQuitServer') {
this.pageNum = 1
this.getWaitPayList(true)
}
}
},
methods: {
...mapMutations({
setQuitOrderTab: 'SET_QUITORDER_TAB'
}),
toOrderDesc (descId) {
this.$router.push('/user/order/orderDesc/' + descId)
},

getDaysByDateString (dateString1, dateString2) {
return Math.abs(moment(dateString1).diff(moment(dateString2), 'days'))
},

getWaitPayList (isFirst) {
const userId = localUser.get('id')
if (userId == null) {
return
}
let orderStatusId = ''
if (this.selectTab === 0) {
orderStatusId = 3
} else if (this.selectTab === 1) {
orderStatusId = 4
} else if (this.selectTab === 2) {
orderStatusId = 5
}
var url = '/v1/orders'
axios.get(this, url, {buyerId: userId, orderStatus: orderStatusId, pageNum: this.pageNum, pageSize: this.pageSize}, (data) => {
for (var i = 0; i < data.length; i++) {
if (data[i].type === 0) {
data[i].liveNum = this.getDaysByDateString(data[i].effectiveDate.split('~')[0], data[i].effectiveDate.split('~')[1])
}
switch (data[i].orderStatus) {
case 3: data[i].orderStatusDesc = '退款中'
break
case 4: data[i].orderStatusDesc = '已退款'
break
case 5: data[i].orderStatusDesc = '退款失败'
break
}
}
if (isFirst) {
this.listOrders = data
this.$nextTick(() => {
if (data.length === 0) {


} else {
this.$refs.scroll.scroll.scrollTo(0, 0)
}
})
return
}

if (data.length === 0) {
this.$vux.toast.text('没有更多数据了', 'middle')
if (this.$refs.scroll && this.pageNum > 1) {
this.$refs.scroll.hiddenPullUpLoad()
} else {
this.$refs.scroll.hiddenPullDown()
}
} else {
if (this.pageNum > 1) {
this.listOrders = this.listOrders.concat(data)
} else {
this.listOrders = data
}
}
})
},

selectTabEvery (value) {
this.setQuitOrderTab(value)
this.pageNum = 1
this.getWaitPayList(true)
},
handleToEvaluation (id) {
this.$router.push('/user/evalution/' + id)
},
handleToProductDesc (id, type) {
this.$router.push('/address/product/' + id + '/' + type)
},
onPullingUp () {
this.pageNum++
this.getWaitPayList()
},
onPullingDown () {
this.pageNum = 1
this.getWaitPayList()
}
},
components: {
VHeader,
Tab,
TabItem,
Sticky,
Divider,
XButton,
Swiper,
SwiperItem,
Flexbox,
FlexboxItem,
Scroll
}
}



